:root{--bgColor: rgb(12, 11, 53);--bgColorLight: rgb(97, 96, 122);--textColor: rgb(244, 244, 248);--textColorDark: hsla(242, 36%, 0%, 1);--paperColor: rgb(30, 130, 155);--paperColorDark: rgb(19, 71, 105);--shadowColorFaint: hsla(0, 0%, 0%, 0.2)}::selected{color:var(--textColorDark)}html,body{margin:0;padding:0;overflow:hidden;height:100vh;width:100vw;background:var(--bgColor);background:linear-gradient(135deg, var(--bgColor), var(--bgColorLight));color:var(--textColor);font-family:'Saira', sans-serif;position:relative}*{box-sizing:border-box;transition:all 0.12s cubic-bezier(0.42, 0.54, 0.22, 1.26)}#canvas{position:fixed;left:0;top:0;width:100%;height:100%}audio{position:fixed;left:10px;bottom:6px;width:calc(100% - 20px)}audio.active{bottom:15px}#thefile{width:0.1px;height:0.1px;opacity:0;overflow:hidden;position:absolute;z-index:1}label.file{display:inline-block;position:absolute;left:50%;top:50%;transform:translate3d(-50%, -50%, 0);padding:1rem 2rem;border-radius:10px;background:var(--paperColor);color:var(--textColor);font-size:1.25em;font-weight:700;box-shadow:0 20px 60px var(--shadowColorFaint);cursor:pointer}label.file:hover{background:var(--paperColorDark);transform:translate3d(-50%, -55%, 0)}label.file:active{background:var(--paperColorDark);transform:translate3d(-50%, -45%, 0)}label.file.normal{transform:translate3d(10%, 50%, 0);padding:.75rem 2rem;font-size:1rem;top:0;left:0}.demo{display:inline-block;position:absolute;left:50%;top:60%;border:none;transform:translate3d(-50%, -50%, 0);padding:1rem 2.3rem;border-radius:10px;background:var(--paperColor);color:var(--textColor);font-size:1.25em;font-weight:700;box-shadow:0 20px 60px var(--shadowColorFaint);cursor:pointer}.demo:active{background:var(--paperColorDark);transform:translate(-50%, -45%)}.demo.normal{transform:translate(130%, 50%);padding:.77rem 2.1rem;font-size:1rem;top:0;left:0}.demo:hover{background:var(--paperColorDark)}
