.modal{position:fixed;display:none;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.6)}.modal-content{background:linear-gradient(45deg, var(--paperColorDark), var(--paperColor));margin:15% auto;padding:20px;border:none;border-radius:70px 10px;width:60%;min-height:20%}.close{color:#aaa;float:right;font-size:28px;font-weight:bold}.close:hover,.close:focus{color:black;text-decoration:none;cursor:pointer}.button{float:right;margin-top:2.3%;left:95%;background:var(--paperColor);color:var(--textColor);font-size:1em;font-weight:700;border:none;box-shadow:0 20px 60px var(--shadowColorFaint);transform:translate3d(-50%, -50%, 0);padding:.8rem 2rem;border-radius:10px;cursor:pointer}.button:hover{background:var(--paperColorDark);transform:translate3d(-50%, -55%, 0)}.info-button{float:right;margin-top:2.3%;left:95%;background:var(--paperColor);color:var(--textColor);font-size:1em;font-weight:700;border:none;box-shadow:0 20px 60px var(--shadowColorFaint);transform:translate3d(-50%, -50%, 0);padding:.8rem 2rem;border-radius:10px;cursor:pointer}.info-button:hover{background:var(--paperColorDark);transform:translate3d(-50%, -55%, 0)}.linked_logo{margin-top:5px;height:20px;width:20px}.github_logo{margin-top:10px;height:20px;width:20px}.modal-links{text-decoration:none;color:var(--textColor)}.modal-words{padding:3px;display:flex;flex-direction:column;justify-content:center}h3{text-align:center}p{text-align:center}.modal-text{line-height:1.75em}.modal-list{list-style:none}
